import React, { Component, useState } from 'react';
import { Table, DatePicker, InputNumber, Button, Form, Modal, message, Row, Col, Menu, Select } from 'antd';
import { EditOutlined, DeleteOutlined, PlusCircleOutlined, ExclamationCircleOutlined } from '@ant-design/icons';
import Flex from 'components/shared-components/Flex'
import { DataService } from "services/taxes.service";
import { Typography } from "antd";
import EllipsisDropdown from 'components/shared-components/EllipsisDropdown';
import NumberFormat from 'react-number-format';

const { Title } = Typography;
const { confirm } = Modal;
const { Option } = Select;

const EditableTable = ({systemData}) => {
  const [form] = Form.useForm();
  const [editingKey, setEditingKey] = useState('');
  const [visible, setVisible] = useState(false);
  const [confirmLoading, setConfirmLoading] = useState(false);

  const { loans, loansLoading, loanPayments, loanPaymentsLoading, allLoans, allLoansLoading, taxes, taxesLoading } = systemData;

  const viewDetails = row => {
    
    setEditingKey(row._id);
    setVisible(true);
    form.setFieldsValue({
      date:row.date,
      name:row.name,
      amount:row.amount,
      
    });
  }

  const dropdownMenu = row => (
		<Menu>
			{/* <Menu.Item onClick={() => {viewDetails(row)}}>
				<Flex alignItems="center">
					<EditOutlined />
					<span className="ml-2">Edit Details</span>
				</Flex>
			</Menu.Item> */}
			<Menu.Item onClick={() => confirmDelete(row)}>
				<Flex alignItems="center">
					<DeleteOutlined />
					<span className="ml-2">Delete</span>
				</Flex>
			</Menu.Item>
		</Menu>
	);

  const confirmDelete = (row) => {
    confirm({
      title: 'Do you Want to delete this tax payment record?',
      icon: <ExclamationCircleOutlined />,
      onOk() {
        remove(row._id);
      }
    });
  };
	

  const showModal = () => {
    setVisible(true);
  };

  const handleOk = async e => {
    try {
      const values = await form.validateFields();
      let date = values.date.format('MM-YYYY');
      let name = values.name.slice(0,1) === ' ' ? values.name.slice(1) : values.name;
      let amount = ''+values.amount;
      if (name ==='' || amount ==='' || date ==='') return;
      setConfirmLoading(true);
      let storeData = { name,amount,date }
      DataService.create(storeData)
      setVisible(false);
      setConfirmLoading(false);
      form.setFieldsValue({name:null,amount:null,date:null});
    } catch (errInfo) {}
    
  };

  const handleCancel = () => {
    setVisible(false);
    setConfirmLoading(false);
    form.setFieldsValue({name:null,amount:null,date:null});
  };
  
  const remove = key => {
    DataService.remove(key)
    message.success(`tax payment info have been deleted`);
  };

  const columns = [
    {
      title: 'Date',
      dataIndex: 'date',
      render: (_,record) => record.data().date
    },
    {
      title: 'Tax name',
      dataIndex: 'name',
      render: (_,record) => record.data().name
    },
    {
      title: 'Amount',
      dataIndex: 'amount',
      render: (_, record) => (
				<div>
					<NumberFormat
						displayType={'text'} 
						value={record.data().amount} 
						thousandSeparator={true} 
					/>
				</div>
			),
    },
    {
			title: '',
			dataIndex: 'actions',
			render: (_, elm) => (
				<div className="text-right">
					<EllipsisDropdown menu={dropdownMenu(elm.data())}/>
				</div>
			)
		}
  ];
  return (
    <Form form={form} component={false} layout="vertical">
      <Flex alignItems="center" justifyContent="between" mobileFlex={false}>
        <Flex className="mb-1" mobileFlex={false}>
          <div className="mr-md-3">
            <Title level={4}>Taxes Paid</Title>
          </div>
        </Flex>
        <Flex mobileFlex={false}>
          <div>
            <Button size={'small'} 
                    style={{
                      marginTop: 0,
                      paddingTop: 0,
                    }}
                    onClick={showModal}
                    type="primary" icon={<PlusCircleOutlined />} block>Add New Payment</Button>
            <Modal
              title="New tax"
              visible={visible}
              onOk={handleOk}
              confirmLoading={confirmLoading}
              onCancel={handleCancel}
            >
              <Row gutter={16}>
                <Col xs={24} sm={24} md={24}>
                  <Form.Item name="name" label="Tax name" rules={[
                    {
                      required: true,
                      message: `name is required.`,
                    },
                  ]}>
                    <Select
                      className="w-100"
                      placeholder="-Select-"
                      >
                        <Option value='TVA'>TVA</Option>
                        <Option value='CIT'>CIT</Option>
                        <Option value='PATENT'>PATENT</Option>
                        
                    </Select>
                  </Form.Item>
                </Col>
                <Col xs={24} sm={24} md={24}>
                  <Form.Item name="amount" label="Amount" rules={[
                    {
                      required: true,
                      message: `Amount is required.`,
                    },
                  ]}>
                    <InputNumber
                      className="w-100"
                      parser={value => value.replace(/\$\s?|(,*)/g, '')}
                      formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
                    />
                  </Form.Item>
                </Col>
                <Col xs={24} sm={24} md={24}>
                  <Form.Item name="date" label="Date" rules={[
                    {
                      required: true,
                      message: `date is required.`,
                    },
                  ]}>
                    <DatePicker className="w-100" format="DD-MM-YYYY"/>
                  </Form.Item>
                </Col>
              </Row>
            </Modal>        
          </div>
        </Flex>
      </Flex>
      <Table
        dataSource={taxesLoading ? [] : taxes.docs.reverse()} 
        columns={columns}
        loading={taxesLoading}
        rowClassName="editable-row"
        size="middle"
      />
    </Form>
  );
};

export class Stores extends Component {
  render() {
    return (
      <EditableTable systemData={this.props.systemData}/>
    )
  }
}

export default Stores
